#badge-txt{
    background-color: #FF6309;
    text-align: center;
    color: white;
}
#badge-txt img{
    width: 30%;
    height: auto;
    margin: auto;    
    
}
#badge-txt button{
    border: none;
    width: 80%;
    padding: 10px 0;
    background-color: #2C2C2C;
    color: white;
    font-size: 20px;
    
}
.all-badges{
    background-image: url("../images/svgbg.png");
    width: 100%;
    height: auto;
    padding: 70px 0;
}
.badgehead{
    text-align: center;
    color: white;
}

.badgehead h1{
    font-family: "Oswald";
    font-size: 72px;
}
.badgehead p{
    font-size: 18px;
    font-family: "Roboto";
}