.quotetxt{
    text-align: center;
    position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  

}
.quotetxt h1{
    font-size: 40px;
}
.quotetxt p{
    font-size: 20px;
}

.quoteimg{
    position: relative;
}

@media screen and (max-width: 480px) {
    .quotetxt h1{
        font-size: 30px;
    }
    .quotetxt p{
        font-size: 14px;
    }
}

@media screen and (min-width: 480px) and (max-width: 768px){
    .quotetxt h1{
        font-size: 40px;
    }
    .quotetxt p{
        font-size: 18px;
    }
}

