#left-txt{
    padding: 280px 50px;
    color: white;
}
#left-txt h2{
    font-family: "Oswald";
    font-size: 70px ;
}
#left-txt p{
    font-family: "Roboto";
    font-size: 20px;
    padding: 20px 0;
}
#left-txt button{
    border: none;
    padding: 10px 100px;
    text-align: center;
    background-color: #2C2C2C;
    color: white;
    font-family: "Oswald";
    line-height: 2.0;
    font-size: 20px;
}
.top-bg{
    background-image: url("../images/gallerytop-bg.png");
    width: 100%;
    height: auto;
}
.bottom-bg{
    background-image: url("../images/gallerydown-bg.png");
    width: 100%;
    height: auto;
}


@media only screen and (max-width: 480px)  {
    #left-txt{
        padding: 100px 50px;
        text-align: center;
    }
}
@media only screen and (min-width: 480px) and (max-width: 768px) {
    #left-txt{
        padding: 100px 50px;
        text-align: center;
    }
}