.hero{
    position: relative;
}
.hero-txt{
    position: absolute;
    bottom: 10%;
    color: white;
    left: 8%;
    width: 40%;
}
.hero-txt h1{
    font-family: 'Oswald', sans-serif;
    font-size: 120px;
}
.hero-txt p{
    font-size: 20px;
    font-family: 'Roboto', sans-serif;

}
.hero-txt button{
    border: none;
    background-color: #EE5E0A;
    padding: 8px 25px;
    color: white;
    font-family: 'Oswald', sans-serif;

}

@media only screen and (max-width: 576px)  {
    .hero-txt h1{
        font-size: 25px;
    }
    .hero-txt p{
        font-size: 12px;
    }
    .hero-txt button{
        padding: 5px 8px;
        font-size: 14px;
    }
    .hero-txt{
        bottom: 10%;
        left: 70px;
    }
}

@media only screen and (min-width: 576px) and (max-width: 768px)    {
    .hero-txt h1{
        
        font-size: 50px;
    }
    .hero-txt p{
        font-size: 14px;
    }
    .hero-txt button{
        padding: 5px 15px;
    }

}
@media only screen and (min-width: 768px) and (max-width: 1200px)    {
    .hero-txt h1{
        
        font-size: 60px;
    }
    .hero-txt p{
        font-size: 18px;
    }
    .hero-txt button{
        padding: 5px 15px;
    }

}