.subscribe{
    text-align: center;
    padding: 150px 0;
    color: white;
    
}
.subscribe h1{
    font-family: "Oswald";
    font-size: 50px;
}
.subscribe p{
    font-family: "Roboto";
    font-size: 18px;
}
.subscribe input{
    width: 60%;
    padding: 10px  0px;
    border-radius: 20px;
    border: none;
}
.subscribe button{
    padding: 12px  25px;
    width: 30%;
    border: none;
    background-color: #2C2C2C;
    color: white;
    margin-left: -70px;
    border-radius: 20px;

}

.emailbg{
    background-image: url("../images/email-bg.png");
    width: 100%;
    height: auto;
}
